@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#BDD6E1}.bio{background-color:#011638;color:#d3d3d3;font-family:"Quicksand",sans-serif}.bio-background{background-attachment:fixed;background-position:center;background-repeat:repeat;background-size:15%;background-color:inherit}.header-card h1{color:#011638;font-family:"Dancing Script",cursive;font-size:4rem}.header-card h4{color:#011638;font-family:"Quicksand",sans-serif}.header-icon{background-color:inherit;border:none;color:white;align-items:'center'}.intro-card{background-color:#364156;color:#d3d3d3;border:solid;border-width:2px;border-color:#011638;margin:10rem}.intro-card h1{font-family:"Dancing Script",cursive;font-size:4rem}.intro-card h3{font-family:"Quicksand",sans-serif}.projectCard{background-color:#364156;color:#d3d3d3;font-family:"Quicksand",sans-serif}.section{background-color:inherit;color:#011638;font-family:"Dancing Script",cursive}#moreButton{font-family:"Quicksand",sans-serif;font-size:1em;background-color:#364156}

